import { initStore } from "../../functions";
import func from "../../functions/src/selfRecorderFunctions";


function preload() {
    return;
}

let arrows = window.selfRecorderRightArrows = [
    { 
        el: null, 
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 50, maxSelector: 100, constantSelectors: { min: 40, max: 80 } },
        notZoomed: { offsetX: 144, offsetY: -268, max: { offsetX: 250 } }, // 106
        zoomed: { offsetX: -355, offsetY: -185, max: { offsetX: -94 } } // 261
    },
    { 
        el: null,
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 100, maxSelector: 200, constantSelectors: { min: 25, max: 125 } },
        notZoomed: { offsetX: 354, offsetY: -268, max: { offsetX: 459 } }, 
        zoomed: { offsetX: 169, offsetY: -185, max: { offsetX: 430 } }
    },
    { 
        el: null, 
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 3, maxSelector: 6, constantSelectors: { min: 2, max: 4 } },
        notZoomed: { offsetX: 144, offsetY: -62, max: { offsetX: 250 } }, 
        zoomed: { offsetX: -357, offsetY: -205, max: { offsetX: -96 } } 
    },
    { 
        el: null,
        elTop: null, 
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 50, maxSelector: 100, constantSelectors: { min: 30, max: 90 } },
        notZoomed: { offsetX: 354, offsetY: -60, max: { offsetX: 458 } }, 
        zoomed: { offsetX: 166, offsetY: -205, max: { offsetX: 427 } }
    },
    { 
        el: null,
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 30, maxSelector: 60, constantSelectors: { min: 15, max: 55 } }, 
        notZoomed: { offsetX: 144, offsetY: 150, max: { offsetX: 249 } }, 
        zoomed: { offsetX: -355, offsetY: -205, max: { offsetX: -96 } }
    },
    { 
        el: null, 
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 40, maxSelector: 80, constantSelectors: { min: 20, max: 60 } },
        notZoomed: { offsetX: 352, offsetY: 150, max: { offsetX: 457 } }, 
        zoomed: { offsetX: 163, offsetY: -203, max: { offsetX: 424 } }
    }
];

function start(sketch, Store) {
    initStore(Store, 'right');

    arrows.forEach((arrow) => {
        arrow.el = sketch.createDiv();
        arrow.el.class('arrow arrow__bottom');
        arrow.elTop = sketch.createDiv();
        arrow.elTop.class('arrow arrow__top');

        arrow.elConstSelectors.forEach((el) => {
            el.elBottom = sketch.createDiv();
            el.elBottom.class('arrow arrow__constant arrow__bottom');
            el.elTop = sketch.createDiv();
            el.elTop.class('arrow arrow__constant arrow__top');
        })

        setTimeout(() => {
            arrow.el.class('arrow arrow__bottom animation-arrow');
            arrow.elTop.class('arrow arrow__top animation-arrow');
        }, 200);

        arrow.elConstSelectors[0].x = arrow.data.constantSelectors.min * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
        arrow.elConstSelectors[1].x = arrow.data.constantSelectors.max * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
    })
    return;
}

let zoomed = false;

function draw(sketch, id, Store) {
    func.recountPosition(arrows, zoomed);
    Store.state.data.selfRecorders.right.forEach((selfRecorder, i) => {
        if(selfRecorder.value !== arrows[i].data.value) {
            arrows[i].data.value = selfRecorder.value;
        }
    })

    if(!window.hiddenRight) {
        switch(id) {
            case 5: {
                func.hideAllArrows();
                zoomed = true;
                func.showArrows(0, 'right', zoomed);
                window.hiddenRight = true;
                break;
            }
            case 6: {
                func.hideAllArrows();
                zoomed = true;
                func.showArrows(2, 'right', zoomed);
                window.hiddenRight = true;
                break;
            }
            case 7: {
                func.hideAllArrows();
                zoomed = true;
                func.showArrows(4, 'right', zoomed);
                window.hiddenRight = true;
                break;
            }
            default: {
                if(id !== -1) {
                    window.hiddenRight = true;
                }
            }
        }
    } else if(id === -1 && window.hiddenRight) {
        func.showArrows(0, 'right');
        func.showArrows(2, 'right');
        func.showArrows(4, 'right');
        window.hiddenRight = false;
        zoomed = false;
        arrows.forEach((arrow) => {        
            arrow.el.class('arrow arrow__bottom animation-arrow');
            arrow.elTop.class('arrow arrow__top animation-arrow');

            arrow.elConstSelectors.forEach((el) => {
                el.elBottom.class('arrow arrow__constant arrow__bottom');
                el.elTop.class('arrow arrow__constant arrow__top');
            })

            arrow.elConstSelectors[0].x = arrow.data.constantSelectors.min * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
            arrow.elConstSelectors[1].x = arrow.data.constantSelectors.max * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
        })
    }

    func.updateArrowsPosition(arrows, zoomed);
}

export default {
    preload,
    start,
    draw
}