export default function smoothScaleImage(SPEED) {
    console.log('[smoothScaleImage] sx:' + this.dataScale.now.scaleX + '   sy:' + this.dataScale.now.scaleY + ' x:' + this.dataScale.now.x + ' y:' + this.dataScale.now.y);
    if(this.dataScale.now.x === this.dataScale.end.endX && this.dataScale.now.y === this.dataScale.end.endY && 
        this.dataScale.now.scaleX === this.dataScale.end.endScaleX && this.dataScale.now.scaleY === this.dataScale.end.endScaleY) {
            this.processScale = false;
            this.dataScale.now.id = this.dataScale.end.id;
            this.dataScale.now.x = 0;
            this.dataScale.now.y = 0;
            this.dataScale.now.scaleX = this.images[this.dataScale.now.id].width - 250; //1216
            this.dataScale.now.scaleY = this.images[this.dataScale.now.id].height - 100;  // 646
            console.log('[smoothScaleImage] ' + this.dataScale.now.scaleX + '   y:' + this.dataScale.now.scaleY);
            return;
        }
        
        console.log("[smoothScaleImage] " + this.dataScale.now.x + ' ' + this.dataScale.now.y);
        if(this.dataScale.now.x > this.dataScale.end.endX) this.dataScale.now.x -= SPEED;
        else this.dataScale.now.x = this.dataScale.end.endX;
        if(this.dataScale.now.y > this.dataScale.end.endY) this.dataScale.now.y -= SPEED;
        else this.dataScale.now.y = this.dataScale.end.endY;
        if(this.dataScale.now.scaleX < this.dataScale.end.endScaleX) this.dataScale.now.scaleX += SPEED + (SPEED / 2);
        else this.dataScale.now.scaleX = this.dataScale.end.endScaleX;
        if(this.dataScale.now.scaleY < this.dataScale.end.endScaleY) this.dataScale.now.scaleY += SPEED;
        else this.dataScale.now.scaleY = this.dataScale.end.endScaleY;
}