<template>
    <div class="menu_wrapper">
        <h2 v-if="exersise">Электронный тренажер</h2>
        <h3 v-if="exersise">для подготовки аппаратчиков <br />химических и нефтехимических производств</h3>
        <h3 v-if="showExersises" class="exersise_subtitle">Устраните нарушения технологического режима</h3>
        <h3 v-if="methodSoprovoj">МЕТОДИЧЕСКОЕ СОПРОВОЖДЕНИЕ</h3>

        <div v-show="exersise">
            <div class="button button__small" @click="() => { showExersises = true; swiperInit(); }">
                Начать
            </div>

            <div class="button button__small button__info" @click="() => { methodSoprovoj = true; }">
                Методическое сопровождение
            </div>
        </div>
        <div class="exersises" v-show="showExersises">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide exersise" v-for="(task, i) in 15" :key="i">
                        <span class="exersise__title">Задание {{ i + 1 }}</span>

                        <div class="button button__small button__exersise" @click="startExersise(i)">
                            Начать                                                              
                        </div>
                    </div>
                </div>

                <div class="swiper-button-next exersises__button-next"></div>
                <div class="swiper-button-prev exersises__button-prev"></div>
            </div>
        </div>

        <div class="finish" v-show="localShowFinish">
            <span class="exersise_finished">Нарушения устранены!</span>

            <div class="button button__small button__continue" @click="() => { showExersises = true; localShowFinish = false; swiperInit(); }">
                Перейти к заданиям
            </div>
        </div>

        <div class="method-soprovojdenia" v-show="methodSoprovoj">
            <AdditionalInformationMenu />
            <div class="button button__small button__info-back" @click="() => { methodSoprovoj = false; }">
                Назад
            </div>
        </div>

        <img class="banner" src="../assets/Menu/banner.jpg" v-show="!methodSoprovoj">
    </div>
</template>

<script>
import './../assets/swiper/swiper-bundle.min.js';
import './../assets/swiper/swiper-bundle.min.css';
import AdditionalInformationMenu from './AdditionalInformationMenu';

export default {
    props: {
        showFinish: Boolean
    },
    data() {
        return {
            showExersises: false,
            swiper: null,
            localShowFinish: this.showFinish,
            methodSoprovoj: false
        }
    },
    components: {
        AdditionalInformationMenu
    },
    methods: {
        startExersise(id) {
            this.$emit('startExersise', id);
        },
        swiperInit() {
            if(this.swiper !== null) return;
            this.swiper = new Swiper('.swiper-container', {
                    slidesPerView: 3,
                    spaceBetween: 20,
                    initialSlide: 1,
                    centeredSlides: true,
                    navigation: {
                            nextEl: '.swiper-button-next',
                            prevEl: '.swiper-button-prev',
                    },
                    observer: true,
                    observeParents: true,
            });
        }
    },
    computed: {
        exersise: function(val) {
            if(!this.localShowFinish && !this.showExersises && !this.methodSoprovoj) {
                return true;
            } else {
                return false;
            }
        }
    }
}
</script>

