import selfRecorderLeft from "./simComponents/selfRecorderLeft";
import selfRecorderRight from "./simComponents/selfRecorderRight";
import panelBar from "./simComponents/panelBar";
import sensorsController from "./simComponents/sensorsController";

function preload(sketch) {
    selfRecorderLeft.preload(sketch);
    selfRecorderRight.preload(sketch);
    panelBar.preload(sketch);
    sensorsController.preload(sketch);
    return;
}

function start(sketch, Store) {
    selfRecorderLeft.start(sketch, Store);
    selfRecorderRight.start(sketch, Store);
    panelBar.start(sketch);
    sensorsController.start(sketch);
    return;
}

function draw(sketch, id, Store) {
    if(id === 4) {
        panelBar.draw(sketch, true, Store);
    }
    if(id === -1) {
        panelBar.draw(sketch, false, Store);
    }
    if(id === 3) {
        sensorsController.draw(sketch, true, Store);
    } else {
        sensorsController.draw(sketch, false, Store);
    }
    
    selfRecorderLeft.draw(sketch, id, Store);
    selfRecorderRight.draw(sketch, id, Store);
    return;
}

function mousePressed(sketch, id, Store) {
    return;
}

function mouseMoved(sketch, id, Store) {
    if(id === 3) {
        sensorsController.mouseMoved(sketch,Store);
    }
}

export default {
    preload,
    start,
    draw,
    mousePressed,
    mouseMoved
}