const DELENIY = 50;

// mutations
const mutations = {
  updateZoneVisible(state, payload) {
    if(payload.typeZone === 'standartZone') {
      state.Zones[payload.zoneID].isVisible = payload.isVisible;
      return;
    } else {
      let id = payload.zoneID;
      if(payload.zoneID >= 100) { id -= 100; }
      state.zoneIncreasesOrDecreases[id].isVisible = payload.isVisible;
    }
  },
  updatePanelbar(state, payload) {
    state.panelBar[payload.id].status = payload.status;
  },
  initStore(state, data) {
    if(data.dir === 'left') {
      data.arrows.forEach((arrow, i) => {
        updateSelfRecorders(state, i, [{id: i, dir: 'left', link: state.selfRecorders.left}], arrow.value, true);
        mutations.zoneIncreaseDecreaseChanged(state, {hoverID: i, value: 0.1});
      })
    } else if(data.dir === 'right') {
      data.arrows.forEach((arrow, i) => {
        updateSelfRecorders(state, i, [{id: i, dir: 'right', link: state.selfRecorders.right}], arrow.value, true);
        mutations.zoneIncreaseDecreaseChanged(state, {hoverID: i, value: 0.1});
      })
    } else {
      throw new Error('[Store data/initStore] Undefined direction on Init Store');
    }
    mutations.zoneIncreaseDecreaseChanged(state, {hoverID: 6, value: 0.1});
    mutations.zoneIncreaseDecreaseChanged(state, {hoverID: 6, value: -0.1});
  },
  zoneIncreaseDecreaseChanged(state, data) {
    var sensorID = state.zoneIncreasesOrDecreases[data.hoverID].sensorID;

    switch(sensorID) {
      case 0: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 0, dir: 'left', link: state.selfRecorders.left, task: null }], data.value);
        break;
      }
      case 1: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 2, dir: 'left', link: state.selfRecorders.left, task: null }], data.value);
        break;
      }
      case 2: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 1, dir: 'left', link: state.selfRecorders.left, task: '+' }, {id: 3, dir: 'left', link: state.selfRecorders.left, task: '+' }], data.value);
        break;
      }
      case 3: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 4, dir: 'left', link: state.selfRecorders.left, task: '-' }, {id: 0, dir: 'right', link: state.selfRecorders.right, task: '+' }], data.value);
        break;
      }
      case 4: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 5, dir: 'left', link: state.selfRecorders.left, task: '-' }, {id: 1, dir: 'right', link: state.selfRecorders.right, task: '+' }], data.value);
        break;
      }
      case 5: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 2, dir: 'right', link: state.selfRecorders.right, task: '-' }, {id: 4, dir: 'right', link: state.selfRecorders.right, task: '+' }], data.value);          
        break;
      }
      case 6: {
        checkMinMaxSensor(state, state.sensors[sensorID].value, sensorID);
        updateSelfRecorders(state, data.hoverID, [{id: 3, dir: 'right', link: state.selfRecorders.right, task: '-' }, {id: 5, dir: 'right', link: state.selfRecorders.right, task: '+' }], data.value);
        break;
      }
    }
  }
} 

const updateSelfRecorders = (state, hoverID, data, value = null, valueSet = false) => {
  if(valueSet === true) {
    data.forEach((selfRecorder) => {
      // let newVal = value;
      // const dir = Math.floor(Math.random() * 2);
      // if(valueSet === true) {
      //   console.log(dir, data);
      //   if(data.id !== 2 && data.dir === 'right') {
  
      //     switch(dir) {
      //       case 0: newVal = value + Math.floor(Math.random() * (20 - 10)) + 10;
      //       default: newVal = value - Math.floor(Math.random() * (20 - 10)) + 10;
      //     }
      //   }
      //   else if(data.id === 1 && data.dir === 'left') {
      //     switch(dir) {
      //       case 0: newVal = value + Math.floor(Math.random() * (200 - 100)) + 100;
      //       default: newVal = value - Math.floor(Math.random() * (200 - 100)) + 100;
      //     }
      //   }
      // }

      selfRecorder.link[selfRecorder.id].value = value;

      checkChangeSelfRecorderValue(selfRecorder);
      changeStatus(state, selfRecorder);
      updateSensorValue(state, selfRecorder.link[selfRecorder.id], state.zoneIncreasesOrDecreases[hoverID].sensorID);
    });
    return;
  }

  if(checkTypeInteraction(state, hoverID)) { // if increase
    data.forEach((selfRecorder) => {
      let changedVal = 0;
      if(value !== 0) changedVal = (selfRecorder.link[selfRecorder.id].max * value) / 100;
      
      if(changedVal === 0) return;

      if(selfRecorder.task === '-') {
        selfRecorder.link[selfRecorder.id].value -=  changedVal;
      }
      else if(selfRecorder.task === '+') {
        selfRecorder.link[selfRecorder.id].value += changedVal;
      } else {
        selfRecorder.link[selfRecorder.id].value += changedVal;
      }
      if(window.debug.recorderTask) console.log(selfRecorder.task); 

      checkChangeSelfRecorderValue(selfRecorder);
      changeStatus(state, selfRecorder);
      updateSensorValue(state, selfRecorder.link[selfRecorder.id], state.zoneIncreasesOrDecreases[hoverID].sensorID);
    })
  } else { // if decrease
    data.forEach((selfRecorder) => {
      let changedVal = 0;
      if(value !== 0) changedVal = (selfRecorder.link[selfRecorder.id].max * value) / 100;
      
      if(changedVal === 0) return;

      if(selfRecorder.task === '-') {
        selfRecorder.link[selfRecorder.id].value += changedVal;
      }
      else if(selfRecorder.task === '+') {
        selfRecorder.link[selfRecorder.id].value -= changedVal;
      } else {
        selfRecorder.link[selfRecorder.id].value -= changedVal;
      }
      checkChangeSelfRecorderValue(selfRecorder);
      changeStatus(state, selfRecorder);
      updateSensorValue(state, selfRecorder.link[selfRecorder.id], state.zoneIncreasesOrDecreases[hoverID].sensorID);
    })
  }

  // console.log('sensorID: ', sensorID, 'hoverID:', hoverID, 'val:', state.selfRecorders.right[5].value, 'step:', changeSelfRecorderValue(state, 5, 'right')); 
}

const checkMinMaxSensor = (state, sensorValue, sensorID) => {
  if(sensorValue < 0) {
    state.sensors[sensorID].value = 0;
  } else if(sensorValue > 100) {
    state.sensors[sensorID].value = 100;
  }
}

const updateSensorValue = (state, selfRecorder, sensorID) => {
  const sensor = state.sensors[sensorID];
  sensor.value = (selfRecorder.value / selfRecorder.max) * 100;
}

const checkChangeSelfRecorderValue = (selfRecorder) => {
  // console.log(selfRecorder.link[selfRecorder.id].value);
  if(selfRecorder.link[selfRecorder.id].value > selfRecorder.link[selfRecorder.id].max) {
    selfRecorder.link[selfRecorder.id].value = selfRecorder.link[selfRecorder.id].max;
    return true;
  } else if(selfRecorder.link[selfRecorder.id].value < 0) {
    selfRecorder.link[selfRecorder.id].value = 0.01;
    return true;
  }
  return false;
} 

