function checkTypeInteraction(state, hoverID) {
  if(state.zoneIncreasesOrDecreases[hoverID].type === 'increase') {
    return true;
  } else if(state.zoneIncreasesOrDecreases[hoverID].type === 'decrease') {
    return false;
  }
  return undefined;
}

