const state = () => ({
    hoveredZoneID: -1,
    selectedZoneID: -1,
    isMousePressed: false
})

const getters = {
  
}

// mutations
const mutations = {
    setHoveredZoneID:  (state, zoneID) => state.hoveredZoneID = zoneID,
    setSelectedZoneID: (state, zoneID) => state.selectedZoneID = zoneID,
    updateMousePressed: (state, payload) => {
        state.isMousePressed = payload
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations
}