import trainerbox from '../../assets/full.png';
import sensors from '../../assets/zoomed/sensor_background.png';
import selfRecorderLeft from '../../assets/selfRecorderLeft.png';
import selfRecorderRight from '../../assets/selfRecorderRight.png';
import sensorsController from '../../assets/sensorsController.png';

import zoomedSelfRecorderLeft_1 from '../../assets/zoomed/selfRecorderLeft_1.png';
import zoomedSelfRecorderLeft_2 from '../../assets/zoomed/selfRecorderLeft_2.png';
import zoomedSelfRecorderLeft_3 from '../../assets/zoomed/selfRecorderLeft_3.png';
import zoomedSelfRecorderRight_1 from '../../assets/zoomed/selfRecorderRight_1.png';
import zoomedSelfRecorderRight_2 from '../../assets/zoomed/selfRecorderRight_2.png';
import zoomedSelfRecorderRight_3 from '../../assets/zoomed/selfRecorderRight_3.png';

let imgs = [
    trainerbox,
    selfRecorderLeft,
    selfRecorderRight,
    zoomedSelfRecorderLeft_1,
    zoomedSelfRecorderLeft_2,
    zoomedSelfRecorderLeft_3,
    sensorsController,
    sensors,
    zoomedSelfRecorderRight_1,
    zoomedSelfRecorderRight_2,
    zoomedSelfRecorderRight_3
]
let loaded = [];

export default function loadImages(sketch) {
    imgs.forEach(img => {
        loaded.push(sketch.loadImage(img));
    });

    return loaded;
}