<template>
    <main>
        <img src="../assets/scheme.png" style="position: relative; left: 50%; transform: translate(-50%);">

        <vue-p5 v-on="{preload, setup, draw, keypressed, mousepressed, mousemoved, mousereleased}" class="AppWrapper"></vue-p5>

        <modal name="menu" @closed="beforeCloseMenu" :clickToClose=false :adaptive=true :width="700" :height="550">
            <Menu @startExersise="startExersise" :showFinish="this.isFinished" />
        </modal>

        <div id="p5_loading" style="position: absolute;">
            Загрузка...<br /><span>Первая загрузка обычно долгая.</span>
        </div>

        <div class="isSmallScreen" v-if="smallScreen">
            Маленький экран, тренажер не помещается. Чтобы его запустить, попробуйте изменить масштаб страницы (зажать одновременно клавишу CTRL и -)
        </div>
    </main>
</template>

<script>
import VueP5 from 'vue-p5';
import f from '../functions';
import simCombiner from '../components/simComponents';
import { mapState } from 'vuex';
import Menu from '../components/Menu';
import smoothScaleImage from '../functions/src/smoothScaleImage';

export default {
    data() {
        return {
            images: [],
            data: this.$store.state.data,
            gameMouse: this.$store.state.gameMouse,
            scaled: false,
            menuIsOpened: true,
            dataScale: {
                now: { id: 0, x:0, y:0, scaleX:1016, scaleY:646 },
                end: { id: 0, endX: 0, endY: 0, endScaleX: 0, endScaleY: 0 }
            },
            processScale: false,
            smoothScaleImage: smoothScaleImage.bind(this),
            smallScreen: false,

            button: {
                back: null,
                scheme: null,
                menu: null,
                style: 'horizontal'
            },

            debug: {
                debugCoords: false,
                coordsNumber: false,
                recorder: false,
                recorderTask: false,
                keys: false
            },
            sketchLink: null,
            signal: null,
            clockTimer: { 
                el: null,
                m:     1,
                h:     1,
                tm:    1,
                s:     0,
                ts:    0,
                init:  0,
                dateObject: null,
                dh: null,
                dm: null,
                ds: null,
                ms: null
            },
            timerText: '00:00:00',
            clockTimerElement: null,
            exersiseNow: -1,
            exersises: {
                updater: null
            },
            isFinished: false
        }
    },
    created() {
        setTimeout(() => {
            if(document.getElementById('p5_loading')) {
                location.reload();
            }
        }, 120000);
    },
    computed: mapState({
        selectedZoneID: state => state.gameMouse.selectedZoneID
    }),
    methods: {
        showMenu(sketch) {
            this.$modal.show('menu');
            sketch.noLoop();
            this.menuIsOpened = true;
            this.timerStop();
        },
        startExersise(id) {
            this.$modal.hide('menu');
            this.isFinished = false;

            let randomBool = () => {
                const result = Math.random() * (3 - 1) + 1;
                return Math.floor(result) === 1;
            };

            f.initStore(this.$store, 'left');
            f.initStore(this.$store, 'right');

            clearInterval(this.exersises.updater);

            window.exersises.done = false;

            this.signal.volume = 1;

            const increaseValue = 3; // in percentages

            window.exersises.id = id;

            switch(id) {
                case 0: {
                    window.exersises.now = 0;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 0, value: randomBool() ? -40 : 30}); 

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 0, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 1: {
                    window.exersises.now = 2;

                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 2, value: randomBool() ? 230 : -500});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 2, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 2: {
                    window.exersises.now = 1;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: randomBool() ? 50 : -50});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 3: {
                    window.exersises.now = 3;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 3, value: 105});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 3, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 4: {
                    window.exersises.now = 4;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: randomBool() ? 80 : -80});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 5: {
                    window.exersises.now = 6;
                    
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 6, value:  randomBool() ? -70 : 60});
                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 6, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 6: {
                    window.exersises.now = 5;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 5, value: randomBool() ? 30 : -40});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 5, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 7: { 
                    window.exersises.now = 104;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 0, value: randomBool() ? 60 : -190}); 
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: randomBool() ? 80 : -80}); 

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 0, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 8: {
                    window.exersises.now = 162;

                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 6, value: randomBool() ? -70 : 60});
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 2, value: randomBool() ? 230 : -500});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 6, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 2, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 9: {
                    window.exersises.now = 135;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 3, value: 105 });
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 5, value: randomBool() ? 30 : -40});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 3, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 5, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 10: {
                    window.exersises.now = 117;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: randomBool() ? 40 : -50});
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 7, value: 105});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 7, value: increaseValue});
                    // }, 3000);
                    break;
                }

                case 11: { 
                    window.exersises.now = 101;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 0, value: randomBool() ? 60 : -190}); 
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: randomBool() ? 40 : -50}); 

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 0, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 12: {
                    window.exersises.now = 152;

                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 5, value: randomBool() ? 30 : -40});
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 2, value: randomBool() ? 230 : -500});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 5, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 2, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 13: {
                    window.exersises.now = 143;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: randomBool() ? 80 : -80});
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 3, value: 105});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 3, value: increaseValue});
                    // }, 3000);
                    break;
                }
                case 14: {
                    window.exersises.now = 114;
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: randomBool() ? 40 : -50});
                    this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: randomBool() ? 80 : -80});

                    // this.exersises.updater = setInterval(() => {
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 1, value: increaseValue});
                    //     this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: 4, value: increaseValue});
                    // }, 3000);
                    break;
                }
            }
        },
        preload(sketch) {
            simCombiner.preload(sketch);

            f.loadImages(sketch).forEach(loadedImage => {
                this.images.push(loadedImage);
            })
        },
        setup(sketch) {
            window.debug = this.debug;
            this.showMenu(sketch);

            window.hidden = false;

            window.exersises = {
                now: -1,
                id: -1,
                done: [],
                doneLength: [ { id: 0, count: 1 }, { id: 2, count: 2 }, { id: 1, count: 1 }, { id: 3, count: 1 }, { id: 4, count: 2 }, { id: 6, count: 1 }, { id: 5, count: 2 },
                    { id: 104, count: 3 }, { id: 162, count: 3 }, { id: 135, count: 3 }, { id: 117, count: 3 }, { id: 101, count: 2 }, { id: 152, count: 4 }, { id: 143, count: 3 }, { id: 114, count: 3 } ]
            };

            sketch.resizeCanvas(1016, 646);
            simCombiner.start(sketch, this.$store);
            sketch.angleMode(sketch.DEGREES);
            this.button.back = sketch.createButton("Назад");
            this.button.back.class('button button__back ' + this.button.style);
            this.button.back.mousePressed(this.selectStandartComponent);

            this.button.scheme = sketch.createButton("Меню");
            this.button.scheme.class('button button__menu ' + this.button.style);
            this.button.scheme.mousePressed(() => {this.showMenu(sketch)});

            this.clockTimerElement = sketch.createInput('00:00:00');
            this.clockTimerElement.attribute('readonly', 'readonly');
            
            if(this.$store.state.gameMouse.selectedZoneID !== -1 ) {
                this.showSelectedZone();
            } else {
                this.selectStandartComponent(); 
            }
            this.sketchLink = sketch;

            window.signalSound = this.signal = new Audio('static/signal.mp3');
            this.signal.loop = true;
            this.signal.autoplay = true;
            this.signal.pause();
        },
        draw(sketch) {
            if(this.menuIsOpened) {
                document.body.style = "overflow: hidden";
            } else {
                document.body.style = "";
            }

            if(sketch.windowWidth < 1250) {
                this.button.style = 'horizontal';
                this.button.scheme.class('button button__menu ' + this.button.style);
                this.clockTimerElement.class('timer ' + this.button.style);
            } else {
                this.button.style = '';
                this.button.scheme.class('button button__menu');
                this.clockTimerElement.class('timer');
            }
            if(sketch.windowWidth < 1015 && !this.smallScreen) {
                document.querySelector('.AppWrapper').style.display = "none";
                this.smallScreen = true;
            }
            else if(sketch.windowWidth > 1015 && this.smallScreen) {
                document.querySelector('.AppWrapper').style.display = "";
                this.smallScreen = false;
            }

            if(window.exersises.done === true) {
                window.exersises.done = false;

                setTimeout(() => {
                    this.selectStandartComponent();
                }, 1000);

                setTimeout(() => {
                    this.selectStandartComponent();
                    this.showMenu(sketch);
                    this.isFinished = true;
                }, 2000);
            }

            // if(window.exersises.done.length !== 0) {
            //     let index = 0;
            //     console.log(window.exersises.done);
            //
            //     while(index !== window.exersises.done.length) {
            //         if(window.exersises.done[index] === true) {
            //             index ++;
            //
            //             if(index >= window.exersises.doneLength[window.exersises.id].count) {
            //                 window.exersises.done = [];
            //
            //                 setTimeout(() => {
            //                     this.selectStandartComponent();
            //                     this.showMenu(sketch);
            //                     this.isFinished = true;
            //                 }, 1000);
            //                 break;
            //             }
            //         }
            //     }
            // }

            sketch.background(200);
            if(!this.scaled) {
                sketch.image(this.images[0], 0, 0, 1016, 646);
                simCombiner.draw(sketch, -1, this.$store);
            } else {
                // const SPEED = 55;

                sketch.image(this.images[this.dataScale.now.id], this.dataScale.now.x, this.dataScale.now.y, this.dataScale.now.scaleX, this.dataScale.now.scaleY);
                simCombiner.draw(sketch, this.selectedZoneID, this.$store);

                if(this.selectedZoneID === 3 && this.$store.state.data.zoneIncreasesOrDecreases[this.gameMouse.hoveredZoneID - 100]?.isVisible === true) {
                    const Zone = this.$store.state.data.zoneIncreasesOrDecreases[this.gameMouse.hoveredZoneID - 100];
                    f.drawCollision(sketch, Zone);

                    if(this.$store.state.gameMouse.isMousePressed) {
                        if(this.gameMouse.hoveredZoneID >= 100 && this.gameMouse.hoveredZoneID <= 106 )
                            this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: this.gameMouse.hoveredZoneID - 100, customTask: '+', value: 1});
                        if(this.gameMouse.hoveredZoneID >= 17 && this.gameMouse.hoveredZoneID <= 113 )
                            this.$store.commit('data/zoneIncreaseDecreaseChanged', {hoverID: this.gameMouse.hoveredZoneID - 100, customTask: '-', value: 1});
                    }
                }

                if(!this.processScale) return;

                // this.smoothScaleImage(SPEED);
            }

            if(!this.scaled && this.$store.state.data.Zones[this.gameMouse.hoveredZoneID]?.isVisible === true) {
                const Zone = this.$store.state.data.Zones[this.gameMouse.hoveredZoneID];
                f.drawCollision(sketch, Zone);
                return;
            } 
        },
        keypressed(sketch) {
            if(!this.debug.keys) return;
            console.log(sketch.key);

            console.log(this.selectedZoneID);
        },
        mousepressed(sketch) {
            if(this.menuIsOpened) return;

            if(this.gameMouse.hoveredZoneID != -1 && this.gameMouse.selectedZoneID === -1) {
                // console.log(this.gameMouse.hoveredZoneID + '   sel:' + this.gameMouse.selectedZoneID);
                this.$store.commit('gameMouse/setSelectedZoneID', this.gameMouse.hoveredZoneID);

                this.showSelectedZone();
            } else if(this.gameMouse.selectedZoneID === 3 && this.gameMouse.hoveredZoneID >= 100) {
                this.$store.commit('gameMouse/updateMousePressed', true)

                if(this.gameMouse.hoveredZoneID === 114) {
                    window.signalSound.pause();
                    window.signalSound.volume = 0;
                    
                }
            }
            
            if(this.debug.debugCoords) {
                console.log(`x${this.debug.coordsNumber + 1}: ${sketch.mouseX}, y${this.debug.coordsNumber + 1}: ${sketch.mouseY}`);
                this.debug.coordsNumber = !this.debug.coordsNumber;
            }


            simCombiner.mousePressed(sketch, this.selectedZoneID, this.$store);
        },
        mousereleased(sketch) {
            if(this.gameMouse.selectedZoneID === 3 && this.gameMouse.isMousePressed) {
                this.$store.commit('gameMouse/updateMousePressed', false)
            }
        },
        mousemoved(sketch) {
            if(!this.scaled) {
                f.checkMouseOverlapZone(sketch, this.$store, 'standartZone');
            }
            if(this.scaled) {
                simCombiner.mouseMoved(sketch, this.selectedZoneID, this.$store);
            }
        },
        selectStandartComponent() {
            this.scaled = false;
            this.$store.commit('gameMouse/setSelectedZoneID', -1);
            this.$store.commit('gameMouse/setHoveredZoneID', -1);
            this.dataScale.now.id = 0;
            this.button.back.class("button button__back " + this.button.style);
        },
        beforeCloseMenu() {
            this.sketchLink.loop();
            this.menuIsOpened = false;
            this.timerToggle();
        },
        timerToggle() {
            this.ClearСlock();
            this.clockTimer.dateObject = new Date();
            this.clockTimer.el = setInterval(() => {this.timerStart() }, 1000);
            this.clockTimer.init = 1;
        },
        timerStop() {
            clearTimeout(this.clockTimer.el);
        },
        timerStart() {
            let cdateObject = new Date();
            const base = 60;
            let t = (cdateObject.getTime() - this.clockTimer.dateObject.getTime()) - (this.clockTimer.s * 1000);
            if (t > 999) {
                this.clockTimer.s++;
            }
            if (this.clockTimer.s >= (this.clockTimer.m * base)) {
                this.clockTimer.ts = 0;
                this.clockTimer.m++;
            } else {
                this.clockTimer.ts = parseInt((this.clockTimer.ms / 100) + this.clockTimer.s);
                if (this.clockTimer.ts >= base) {
                    this.clockTimer.ts = this.clockTimer.ts - ((this.clockTimer.m - 1) * base);
                }
            }
            if (this.clockTimer.m > (this.clockTimer.h * base)) {
                this.clockTimer.tm = 1;
                this.clockTimer.h++;
            } else {
                this.clockTimer.tm = parseInt((this.clockTimer.ms / 100) + this.clockTimer.m);
                if (this.clockTimer.tm >= base) {
                this.clockTimer.tm = this.clockTimer.tm - ((this.clockTimer.h - 1) * base);
                }
            }
            this.clockTimer.ms = Math.round(t / 10);
            if (this.clockTimer.ms > 99) {
                this.clockTimer.ms = 0;
            }
            if (this.clockTimer.ms == 0) {
                this.clockTimer.ms = '00';
            }
            if (this.clockTimer.ms > 0 && this.clockTimer.ms <= 9) {
                this.clockTimer.ms = '0' + this.clockTimer.ms;
            }
            if (this.clockTimer.ts > 0) {
                this.clockTimer.ds = this.clockTimer.ts;
                if (this.clockTimer.ts < 10) {
                this.clockTimer.ds = '0' + this.clockTimer.ts;
                }
            } else {
                this.clockTimer.ds = '00';
            }
            this.clockTimer.dm = this.clockTimer.tm - 1;
            if (this.clockTimer.dm > 0) {
                if (this.clockTimer.dm < 10) {
                this.clockTimer.dm = '0' + this.clockTimer.dm;
                }
            } else {
                this.clockTimer.dm = '00';
            }
            this.clockTimer.dh = this.clockTimer.h - 1;
            if (this.clockTimer.dh > 0) {
                if (dh < 10) {
                this.clockTimer.dh = '0' + this.clockTimer.dh;
                }
            } else {
                this.clockTimer.dh = '00';
            }
            this.timerText = this.clockTimer.dh + ':' + this.clockTimer.dm + ':' + this.clockTimer.ds;
            this.clockTimerElement.value(this.timerText);
        },
        ClearСlock() {
            clearTimeout(this.clockTimer.el);
        },
        showSelectedZone() {
            switch(this.selectedZoneID) {
                case 0: {
                    this.dataScale.now = { id: 3, x: 0, y:0, scaleX: this.images[3].width - 200, scaleY: this.images[3].height - 100 };
                    break;
                }
                case 1: {
                    this.dataScale.now = { id: 4, x: 0, y: 0, scaleX: this.images[4].width - 200, scaleY: this.images[4].height - 100 };
                    break;
                }
                case 2: {
                    this.dataScale.now = { id: 5, x: 0, y: 0, scaleX: this.images[5].width - 200, scaleY: this.images[5].height - 100 };
                    break;
                }
                case 3: {
                    this.dataScale.now = { id: 6, x: 508 - ((this.images[6].width - 250) / 2), y: 0, scaleX: this.images[6].width - 250, scaleY: 646 + 20 };
                    break;
                }
                case 4: {
                    this.dataScale.now = { id: 7, x: 508 - ((this.images[7].width + 400) / 2), y: 0, scaleX: this.images[7].width + 400, scaleY: this.images[7].height + 400 };
                    break;
                }
                case 5:
                    this.dataScale.now = { id: 8, x: 0, y: 0, scaleX: this.images[8].width - 200, scaleY: this.images[8].height - 100 };
                    this.dataScale.end.id = 8;
                break;
                case 6:
                    this.dataScale.now = { id: 9, x: 0, y: 0, scaleX: this.images[9].width - 200, scaleY: this.images[9].height - 100 };
                    this.dataScale.end.id = 9;
                break;
                case 7:
                    this.dataScale.now = { id: 10, x: 0, y: 0, scaleX: this.images[10].width - 200, scaleY: this.images[10].height - 100 };
                    this.dataScale.end.id = 10;
                break;
            }
            
            this.scaled = true;
            this.button.back.class("button button__back active " + this.button.style);
        }
    },
    components: {
        VueP5,
        Menu
    }
}
</script>