import { initStore } from "../../functions";
import func from "../../functions/src/selfRecorderFunctions";

function preload() {
    return;
}

let arrows = window.selfRecorderLeftArrows = [
    { 
        el: null, 
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 325, maxSelector: 650, constantSelectors: { min: 200, max: 400 } },
        notZoomed: { x: 0, offsetX: -471, offsetY: -263, max: { offsetX: -365 } }, // 106
        zoomed: { x: 0, offsetX: -414, offsetY: -176, max: { offsetX: -153 } } // 261
    },
    { 
        el: null,
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 300, maxSelector: 600, constantSelectors: { min: 200, max: 400 } },
        notZoomed: { x: 0, offsetX: -260, offsetY: -270, max: { offsetX: -156 } },  
        zoomed: { x: 0, offsetX: 107, offsetY: -187, max: { offsetX: 368 } } 
    },
    { 
        el: null, 
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 50, maxSelector: 100, constantSelectors: { min: 20, max: 80 } },
        notZoomed: { x: 0, offsetX: -471, offsetY: -57, max: { offsetX: -365 } }, 
        zoomed: { x: 0, offsetX: -412, offsetY: -202, max: { offsetX: -151 } } 
    },
    { 
        el: null,
        elTop: null, 
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 900, maxSelector: 1800, constantSelectors: { min: 500, max: 1100 } },
        notZoomed: { x: 0, offsetX: -260, offsetY: -57, max: { offsetX: -154 } }, 
        zoomed: { x: 0, offsetX: 111, offsetY: -202, max: { offsetX: 372 } }
    },
    { 
        el: null,
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 150, maxSelector: 300, constantSelectors: { min: 140, max: 240 } }, 
        notZoomed: { x: 0, offsetX: -466, offsetY: 151, max: { offsetX: -361 } }, 
        zoomed: { x: 0, offsetX: -402, offsetY: -202, max: { offsetX: -141 } }
    },
    { 
        el: null, 
        elTop: null,
        elConstSelectors: [
            { x: 0, elBottom: null, elTop: null },
            { x: 0, elBottom: null, elTop: null }
        ],
        data: { value: 40, maxSelector: 80, constantSelectors: { min: 30, max: 70 } },
        notZoomed: { x: 0, offsetX: -260, offsetY: 150, max: { offsetX: -154 } }, 
        zoomed: { x: 0, offsetX: 113, offsetY: -203, max: { offsetX: 374 } }
    }
];

function start(sketch, Store) {
    initStore(Store, 'left');

    arrows.forEach((arrow) => {
        arrow.el = sketch.createDiv();
        arrow.el.class('arrow arrow__bottom');

        arrow.elTop = sketch.createDiv();
        arrow.elTop.class('arrow arrow__top');
        

        arrow.elConstSelectors.forEach((el) => {
            el.elBottom = sketch.createDiv();
            el.elBottom.class('arrow arrow__constant arrow__bottom');
            el.elTop = sketch.createDiv();
            el.elTop.class('arrow arrow__constant arrow__top');
        })

        setTimeout(() => {
            arrow.el.class('arrow arrow__bottom animation-arrow');
            arrow.elTop.class('arrow arrow__top animation-arrow');
        }, 50);

        arrow.elConstSelectors[0].x = arrow.data.constantSelectors.min * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
        arrow.elConstSelectors[1].x = arrow.data.constantSelectors.max * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
    })
}

let zoomed = false;

function draw(sketch, id, Store) {
    func.recountPosition(arrows, zoomed);
    Store.state.data.selfRecorders.left.forEach((selfRecorder, i) => {
        if(selfRecorder.value !== arrows[i].data.value) {
            arrows[i].data.value = selfRecorder.value;
        }
    });

    if(!window.hiddenLeft) {
        switch(id) {
            case 0: {
                func.hideAllArrows();
                zoomed = true;
                func.showArrows(id, 'left', zoomed);
                window.hiddenLeft = true;
                break;
            }
            case 1: {
                func.hideAllArrows();
                zoomed = true;
                func.showArrows(2, 'left', zoomed);
                window.hiddenLeft = true;
                break;
            }
            case 2: {
                func.hideAllArrows();
                zoomed = true;
                func.showArrows(4, 'left', zoomed);
                window.hiddenLeft = true;
                break;
            }
            default: {
                if(id !== -1) {
                    window.hiddenLeft = true;
                }
            }
        }
    } else if(id === -1 && window.hiddenLeft) {
        func.showArrows(0, 'left');
        func.showArrows(2, 'left');
        func.showArrows(4, 'left');
        window.hiddenLeft = false;
        zoomed = false;
        
        arrows.forEach((arrow) => {        
            arrow.el.class('arrow arrow__bottom animation-arrow');
            arrow.elTop.class('arrow arrow__top animation-arrow');

            arrow.elConstSelectors.forEach((el) => {
                el.elBottom.class('arrow arrow__constant arrow__bottom');
                el.elTop.class('arrow arrow__constant arrow__top');
            })

            arrow.elConstSelectors[0].x = arrow.data.constantSelectors.min * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
            arrow.elConstSelectors[1].x = arrow.data.constantSelectors.max * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
        })

    }

    func.updateArrowsPosition(arrows, zoomed);
}

/*
* x = Селектор * ( (max.offsetX - offsetX) / maxSelector ) + offsetX
*/

export default {
    preload,
    start,
    draw
}
