const DIST_NOT_ZOOMED = 25;
const DIST_ZOOMED = 55;

function updateArrowsPosition(arrows, zoomed) {
	arrows.forEach((arrow) => {
		arrow.el.style('margin-left', `${!zoomed ? arrow.notZoomed.x : arrow.zoomed.x}px`);
		arrow.el.style('margin-top', `${!zoomed ? arrow.notZoomed.offsetY : arrow.zoomed.offsetY}px`);
		arrow.elTop.style('margin-left', `${!zoomed ? arrow.notZoomed.x : arrow.zoomed.x}px`);
		arrow.elTop.style('margin-top', `${!zoomed ? arrow.notZoomed.offsetY - DIST_NOT_ZOOMED : arrow.zoomed.offsetY - DIST_ZOOMED}px`);

		arrow.elConstSelectors.forEach((el) => {
			el.elBottom.style('margin-left', `${el.x}px`);
			el.elBottom.style('margin-top', `${!zoomed ? arrow.notZoomed.offsetY : arrow.zoomed.offsetY}px`);
			el.elTop.style('margin-left', `${el.x}px`);
			el.elTop.style('margin-top', `${!zoomed ? arrow.notZoomed.offsetY - DIST_NOT_ZOOMED : arrow.zoomed.offsetY - DIST_ZOOMED}px`);
		})
	})
}

function recountPosition(arrows, zoomed) {
	arrows.forEach((arrow) => {
		// 0 = arrow.offsetX   |   100 = arrow.max.offsetX
		// data.selector   |   data.maxSelector
		if(!zoomed) {
			arrow.notZoomed.x = arrow.data.value * ((arrow.notZoomed.max.offsetX - arrow.notZoomed.offsetX) / arrow.data.maxSelector) + arrow.notZoomed.offsetX;
		}
		else {
			arrow.zoomed.x = arrow.data.value * ((arrow.zoomed.max.offsetX - arrow.zoomed.offsetX) / arrow.data.maxSelector) + arrow.zoomed.offsetX;	

			arrow.elConstSelectors[0].x = arrow.data.constantSelectors.min * ((arrow.zoomed.max.offsetX - arrow.zoomed.offsetX) / arrow.data.maxSelector) + arrow.zoomed.offsetX;
			arrow.elConstSelectors[1].x = arrow.data.constantSelectors.max * ((arrow.zoomed.max.offsetX - arrow.zoomed.offsetX) / arrow.data.maxSelector) + arrow.zoomed.offsetX;
			arrow.elConstSelectors[0].x = arrow.data.constantSelectors.min * ((arrow.zoomed.max.offsetX - arrow.zoomed.offsetX) / arrow.data.maxSelector) + arrow.zoomed.offsetX;
			arrow.elConstSelectors[1].x = arrow.data.constantSelectors.max * ((arrow.zoomed.max.offsetX - arrow.zoomed.offsetX) / arrow.data.maxSelector) + arrow.zoomed.offsetX;
		}
	})
}


function hideAllArrows() {
    window.selfRecorderLeftArrows.forEach((arrow) => {
        arrow.el.style('display', 'none');
        arrow.elTop.style('display', 'none');
        arrow.elConstSelectors.forEach((el) => {
            el.elBottom.style('display', 'none');
            el.elTop.style('display', 'none');
        })
	})
	
	window.selfRecorderRightArrows.forEach((arrow) => {
        arrow.el.style('display', 'none');
        arrow.elTop.style('display', 'none');
        arrow.elConstSelectors.forEach((el) => {
            el.elBottom.style('display', 'none');
            el.elTop.style('display', 'none');
        })
    })
}

function showArrows(id, dir, zoomed = false) {
	let arrows = dir === 'left' ? window.selfRecorderLeftArrows : window.selfRecorderRightArrows;

    arrows[id].el.style('display', 'block');
    arrows[id].elTop.style('display', 'block');
    arrows[id + 1].el.style('display', 'block');
    arrows[id + 1].elTop.style('display', 'block');

    if(zoomed) {
        arrows[id].el.class('arrow arrow__bottom arrow__scaled');
        arrows[id].elTop.class('arrow arrow__top arrow__scaled');
        arrows[id + 1].el.class('arrow arrow__bottom arrow__scaled');
        arrows[id + 1].elTop.class('arrow arrow__top arrow__scaled');

        setTimeout(() => {
            arrows[id].el.class('arrow arrow__bottom arrow__scaled animation-arrow');
            arrows[id].elTop.class('arrow arrow__top arrow__scaled animation-arrow');
            arrows[id + 1].el.class('arrow arrow__bottom arrow__scaled animation-arrow');
            arrows[id + 1].elTop.class('arrow arrow__top arrow__scaled animation-arrow');
        }, 9)
    }

    constantArrowsShow(id, dir, zoomed);
}

function constantArrowsShow(id, dir, zoomed = false) {
	let arrows = dir === 'left' ? window.selfRecorderLeftArrows : window.selfRecorderRightArrows;

    arrows[id].elConstSelectors.forEach((el) => {
        el.elBottom.style('display', 'block');
        el.elTop.style('display', 'block');
    })
    arrows[id + 1].elConstSelectors.forEach((el) => {
        el.elBottom.style('display', 'block');
        el.elTop.style('display', 'block');
    })

    if(zoomed) {
        arrows[id].elConstSelectors.forEach((el) => {
            el.elBottom.class('arrow arrow__bottom arrow__scaled arrow__constant');
            el.elTop.class('arrow arrow__top arrow__scaled arrow__constant');
        })
        arrows[id + 1].elConstSelectors.forEach((el) => {
            el.elBottom.class('arrow arrow__bottom arrow__scaled arrow__constant');
            el.elTop.class('arrow arrow__top arrow__scaled arrow__constant');
        })
    }
}

export default {
	updateArrowsPosition,
	recountPosition,
	hideAllArrows,
	showArrows
}