function preload(sketch) {
    sensorActive = sketch.loadImage(path_sensorActive);
    sensorInactive = sketch.loadImage(path_sensorInactive);
    return;
}

function start(sketch) {
    return;
}

function draw(sketch, zoomed, Store) {
    if(zoomed) {        
        if(window.hiddenLeft === false && window.hiddenRight === false) {
            window.hiddenLeft = true;
            window.hiddenRight = true;
    
            func.hideAllArrows();
        }
    }
    sensors.forEach((sensor, i) => {
        if(sensor.status !== Store.state.data.panelBar[i].status) {
            sensor.status = Store.state.data.panelBar[i].status;
        }
        sketch.image(sensor.status ? sensorActive : sensorInactive, 
            zoomed ? sensor.position.x : sensor.position.notZoomed.x, 
            zoomed ? sensor.position.y : sensor.position.notZoomed.y,
            zoomed ? sensor.size.width : sensor.size.notZoomed.width,
            zoomed ? sensor.size.height : sensor.size.notZoomed.height);

        sketch.push();
            sketch.textAlign(sketch.CENTER, sketch.CENTER);
            sketch.textSize(zoomed ? sensor.size.text : sensor.size.notZoomed.text);
            sketch.textStyle(sketch.BOLD);
            sketch.text(sensor.name, 
                zoomed ? sensor.position.textX : sensor.position.notZoomed.textX, 
                zoomed ? sensor.position.textY : sensor.position.notZoomed.textY);
        sketch.pop();
        })
}

export default {
    preload,
    start,
    draw
}