import getSize from './getSize';

function drawCollision(sketch, Zone) {
    if(!Zone?.x2) return;

    sketch.push();
        sketch.fill(0, 0, 0, 0);
        sketch.stroke(255, 39, 50);
        sketch.strokeWeight(2);
        sketch.rect(Zone.x2, Zone.y2, getSize(Zone.x1, Zone.x2), getSize(Zone.y1, Zone.y2));
    sketch.pop();
}

export default drawCollision