let lockUpdate = false;
var id;

export default function checkMouseOverlapZone(sketch, Store, typeZone) {
    let Zones = typeZone === 'standartZone' ? Store.state.data.Zones : Store.state.data.zoneIncreasesOrDecreases;
    UpdateID(Store, typeZone);
    if(Store.state.gameMouse.hoveredZoneID === -1) {  
        Zones.forEach(obj => { 
            if (
                sketch.mouseX > obj.x1 &&
                sketch.mouseX < obj.x2 &&
                sketch.mouseY > obj.y1 &&
                sketch.mouseY < obj.y2
            ) {
                UpdateID(Store, typeZone);

                Store.commit('gameMouse/setHoveredZoneID', obj.id);
                Store.commit('data/updateZoneVisible', {zoneID: obj.id, isVisible: true, typeZone});
            } 
        });
    } else if(Store.state.gameMouse.hoveredZoneID === 3) {
        Zones.forEach(obj => { 
            if (
                sketch.mouseX > obj.x1 &&
                sketch.mouseX < obj.x2 &&
                sketch.mouseY > obj.y1 &&
                sketch.mouseY < obj.y2
            ) {
                UpdateID(Store, typeZone);

                Store.commit('gameMouse/setHoveredZoneID', obj.id);
                Store.commit('data/updateZoneVisible', {zoneID: obj.id, isVisible: true, typeZone});
            } 
        });
    } else {
        if(lockUpdate == true) return;

        if (
            sketch.mouseX > Zones[id].x1 &&
            sketch.mouseX < Zones[id].x2 &&
            sketch.mouseY > Zones[id].y1 &&
            sketch.mouseY < Zones[id].y2
        ) {
            lockUpdate = true;
            setTimeout(() => { lockUpdate = false }, 80);
            return;
        } 
        else {
            UpdateID(Store, typeZone);
            Store.commit('data/updateZoneVisible', {zoneID: id, isVisible: false, typeZone});
            Store.commit('gameMouse/setHoveredZoneID', -1);
        }
    }

    // console.log('hover: ' + Store.state.gameMouse.hoveredZoneID);
}

function UpdateID(Store, typeZone) {
    id = typeZone === 'standartZone' ? Store.state.gameMouse.hoveredZoneID : Store.state.gameMouse.hoveredZoneID - 100;
}