            <div v-show="annotacia">
                <p>Данный электронный тренажер для подготовки аппаратчиков химических и нефтехимических производств разработан для использования на уроках производственного обучения по специальности Переработка химического (нефтехимического) сырья по единичным квалификациям: аппаратчик газоразделения, аппаратчик полимеризации для повышения мотивации учащихся и формированию у них трудовых умений и навыков.</p>
                <p>Современное, бурно развивающееся производство вызывает коренные изменения в содержании труда квалицированного рабочего. В этих условиях применение традиционных методов и средств обучения уже не может в полной мере обеспечить качественную подготовку рабочих высокой квалификации. Специфика учебного процесса состоит в том, что формирование профессиональных навыков и умений требует многократного повторения осваиваемых действий, выполняемых под непрерывным контролем со стороны мастера производственного обучения.</p>
                <p>Для целей обучения часто нецелесообразно использовать дорогостоящее производственное оборудование. Кроме того, имитация с их помощью в учебных целях различных отклонений от нормальных технологических параметров связана с возможностью серьезных аварий и большим риском для жизни учащихся и мастера производственного обучения.</p>
                <p>Решением подобных проблем может стать использование электронного тренажера. Применение тренажера позволяет многократно моделировать и прогнозировать помехи и неисправности до полного их устранения, позволяет задавать учащимся, повторять и варьировать нужные режимы работы оборудования и производственные ситуации.</p>
            </div>
            <div v-show="zapiska">
                <p>Электронный тренажер для подготовки аппаратчиков химических и нефтехимических производств предназначен для обучения учащихся профессионально-технических учебных заведений первоначальным навыкам по ведению технологического процесса ректификации бинарной смеси.</p>
                <p>Учащиеся, получая по показаниям контрольно-измерительных приборов информацию о нарушении того или иного параметра, принимают определенное решение и возвращают процесс к нормальному режиму, что осуществляется при помощи панели управления. За правильностью действий учащихся мастер следит по показаниям контрольно-измерительных приборов.</p>
                <p>Электронный тренажер призван помочь мастерам производственного обучения в организации занятий с учащимся, при освоении навыков по поддержанию заданного технологического режима и практических навыков по управлению процессом ректификации бинарной смеси.</p>
                <p>Электронный обучающий ресурс содержит табло сигнализации, панель управления, самопишущие приборы, мнемосхему процесса ректификации бинарной смеси.</p>
                <p>Проведение уроков производственного обучения на электронном тренажере способствует повышению эффективности восприятия информации, повышению познавательной деятельности учащихся, формированию профессиональных умений и навыков за счет многократного повторения отрабатываемых действий.</p>
