            <div v-show="naznachenie">
                <p>Электронный тренажер моделирует нормальный технологический режим процесса ректификации бинарной смеси, позволяет имитировать его нарушения с отображением контролируемых параметров самопишущими приборами и управлять параметрами процесса с помощью панели управления.</p>
                <p><em>Основные зада</em>чи, решаемые на электронном тренажере:</p>
                <p>- формирование навыков по поддержанию заданного технологического режима. Отработку этих навыков можно осуществлять при любом сочетании параметров: давления, температуры, уровня и расхода;</p>
                <p>- наблюдение и снятие показаний КИП;</p>
                <p>- обучение умению правильной оценки по показаниям КИП состояния технологического процесса и определению способов управления процессом;</p>
                <p>- приобретение первоначальных практических навыков по управлению процессом с помощью органов ручного управления.</p>
                <p><em>Контролируемые параметры:</em> давление, расход, уровень, температура.</p>
