                <p>входные:</p>
                <p>расход орошения,</p>
                <p>расход сырья на установку,</p>
                <p>температура сырья на входе,</p>
                <p>температура в кубе колонны,</p>
                <p>расход теплоносителя в ребойлер,</p>
                <p>расход хладоагента;</p>
                <p>выходные:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; уровень в дефлегматоре,</p>
                <p>расход дистиллята,</p>
                <p>температура верха колонны,</p>
                <p>давление в колонне,</p>
                <p>уровень в кубе колонны,</p>
                <p>расход кубового продукта.</p>
                <p><em>Описание конструкции</em></p>
                <p>Электронный тренажер представляет собой щит аппаратчика технологической установки, на лицевой панели которого установлены двенадцать автоматических потенциометров, имитирующих вторичные приборы реальных схем автоматического контроля и управления, табло сигнализации, панель управления. В верхней части электронного тренажера установлена мнемосхема процесса ректификации бинарной смеси.</p>
                <p><em>Принцип работы тренажера</em></p>
                <p>Тренажер воспроизводит упрощенную динамическую модель процесса ректификации бинарной смеси.</p>
                <p>Устранение нарушений технологического процесса производится обучаемым при помощи панели управления согласно показаниям контрольно-измерительных приборов или автоматически в соответствующем режиме.</p>
                <p>При нарушениях технологического режима срабатывает звуковая и световая сигнализация. Звуковую сигнализацию можно отключать кнопкой.</p>
                <p><em>Панель управления содержит:</em></p>
                <p>- индикатора положения унифицированные, показывающие степень открытия регулирующих органов на технологических трубопроводах.</p>
                <p><em>Табло сигнализации</em> (рис.1).</p>
                <p>Конструктивно табло представляет собой блок, на лицевой панели которого размещены 10 световых указателей с надписями, раскрывающими виды нарушений.</p>
                <img style="display: block; margin: 0 auto;" src="static/methods_soprovojdenia/table_signal.png">
                <p style="text-align: center;"><em>Рис.1. Табло сигнализации</em></p>
                <p><em>Мнемосхема</em></p>
                <p>Мнемосхема (рис.2) представляет собой щит с изображением упрощенной схемы технологического процесса с нанесением основных приборов КИПиА органов ручного управления процессом.</p>
                <img style="display: block; margin: 0 auto; width: 550px;" src="static/methods_soprovojdenia/mnemoscheme.jpg">
                <p style="text-align: center;"><em>Рис.2 Мнемосхема</em></p>
                <p>Основное технологическое оборудование на мнемосхеме обозначено следующим образом:</p>
                <p>а &ndash; теплообменник (подогреватель исходной смеси)</p>
                <p>б &ndash; ректификационная колонна</p>
                <p>в &ndash; кипятильник (ребойлер)</p>
                <p>г &ndash; дефлегматор</p>
                <p>д &ndash; холодильник</p>
                <p>е &ndash; насос</p>
                <p>Номера приборов КИП и А, изображенных на мнемосхеме, состоят из двух цифр. Первая цифра &ndash; порядковый номер&nbsp; контура контроля и автоматического управления, вторая цифра &ndash; порядковый номер соответствующего прибора КИП и А в контуре.</p>
                <p>На изображениях всех приборов КИП и А кроме регулирующих органов помимо цифровых обозначений имеются буквенные.</p>
                <p>Буквы внутри окружностей, обозначают следующее:</p>
                <p>Первая буква:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; T &ndash; температура</p>
                <p>P &ndash; давление</p>
                <p>F &ndash; расход</p>
                <p>L &ndash; уровень</p>
                <p>Вторая буква:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Е &ndash; первичный преобразователь, чувствительный элемент</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Т &ndash; промежуточное преобразование, дистанционная передача</p>
                <p>R &ndash; регистрация</p>
                <p>Третья буква:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; С &ndash; регулирование, управление</p>
                <p>Четвертая буква:&nbsp;&nbsp;&nbsp; А - сигнализация</p>
                <p>Буквы, находящиеся за пределами окружностей, означают следующее:</p>
                <p>Н &ndash; верхний предел измеряемой величины</p>
                <p>L &ndash; нижний предел измеряемой величины</p>
                <p><em>Примеры условных обозначений</em>:</p>
                <p>ТЕ &ndash; первичный преобразователь температуры, установленный по месту (термопара, термометр сопротивления).</p>
                <p>РТ &ndash; прибор для измерения давления бесшкальный с дистанционной передачей показаний (манометр, дифманометр с пневмо- или электропередачей).</p>
                <p>FЕ &ndash; первичный преобразователь расхода (диафрагма).</p>
                <p>FС &ndash; прибор для измерения расхода, регистрирующий, регулирующий, установленный на щите.</p>
                <p>ТRСА<sup>Н</sup> &ndash; прибор для измерения температуры, регистрирующий, регулирующий, с сигнализацией по верхнему значению температуры, установленный на щите.</p>
                <p><em>Описание мнемосхемы</em></p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Разделение жидких однородных смесей (растворов) состоящих из 2-х или большего числа летучих компонентов производящихся перегонкой или ректификацией.</p>
                <p><em>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Ректификацией </em>называется процесс многократного испарения и конденсации паров, проводимый в ректификационных установках периодического или непрерывного действия с целью полного разделения компонентов смеси. Если исходную смесь, состоящую из жидкостей с различными температурами кипения, частично испарить, а затем образовавшийся пар конденсировать, то полученная жидкость будет отличаться по составу более высоким содержанием легколетучего компонента (НК). В оставшейся исходной смеси содержится большее количество труднолетучего или высококипящего компонента (ВК) - эта жидкость называется остатком и называется дистиллятом или ректификатом.</p>
                <p><em>Описание схемы: </em></p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Исходная 2-х компонентная смесь в подогревателе - <strong>а</strong> нагревается до температуры кипения и поступает в ректификационную колонну-<strong>б</strong>. В колонне расположены контактные устройства, с помощью которых поднимающийся пар входит в соприкосновение с флегмой, получаемой в дефлегматоре - <strong>г</strong>.</p>
                <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Паровая и жидкая фазы взаимодействуют следующим образом: пары низколетучего компонента смеси частично конденсируются и теплота, выделяющаяся при их конденсации, расходуется на испарение части легкокипящего компонента и стекающей вниз флегмы. Этот процесс в колонне повторяется многократно на каждой тарелке, по мере того, как пары поднимаются вверх по колонне. В результате взаимодействия пары поднимающиеся из верхней части колонны, значительно обогащаются легколетучим компонентом. Эти пары, проходя через холодильник &ndash; <strong>д,</strong> конденсируются и поступают в дефлегматор - <strong>г</strong>, где происходит разделение на жидкую и газовую фазы. Поток полученной жидкости делится на 2 потока: один поток возвращается в виде флегмы обратно в колонну для орошения, а второй поток - согласно технологическому режиму (дистиллят)- состоящий из легколетучего компонента. Флегма, стекающая в нижнюю часть колонны и содержащая в основном высококипящий компонент (ВК) собирается в кубе колонны и называется кубовым остатком.</p>
                <p>Таким образом, в результате процесса ректификации получают довольно полное разделение компонентов смеси: дистиллят (НК) + кубовый остаток (ВК).</p>
                <p>Кубовый остаток так же разделяется на 2 части: одна часть откачивается в приёмник, вторая - через ребойлер в виде пара возвращается в колонну.</p>
                <p>Точку ввода исходной смеси выбирают так, чтобы состав исходной смеси соответствовал составу жидкости на данной тарелке.</p>
