                <p>Переходим к следующему заданию</p><br/>
            </div>
        </div>
        <div class="tabs tabs-down">
            <span :class="{ 'active': instruction }" @click="changeActiveTab('instruction')">Инструкция</span>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            annotacia: true,
            zapiska: false,
            naznachenie: false,
            sources: false,
            instruction: false
        }
    },
    methods: {
        changeActiveTab: function(name) {
            this.annotacia = false; 
            this.zapiska = false; 
            this.naznachenie = false; 
            this.sources = false; 
            this.instruction = false;
            switch(name) {
                case 'annotacia': return this.annotacia = true;
                case 'zapiska': return this.zapiska = true;
                case 'naznachenie': return this.naznachenie = true;
                case 'sources': return this.sources = true;
                case 'instruction': return this.instruction = true;
            }
        }
    }
}
</script>

