export default function initStore(Store, dir) {
    let arrowEdited = [];
    dir === 'left' ? selfRecorderLeftArrows.forEach((arrow) => {
        arrowEdited.push({value: arrow.data.value});
	}) : selfRecorderRightArrows.forEach((arrow) => {
        arrowEdited.push({value: arrow.data.value});
	});

    Store.commit('data/initStore', { dir: dir, arrows: arrowEdited });
}